.generate-image-form {
  display: flex;
  justify-content: space-between;
  margin: 20px;
}

.input-side {
  flex: 1;
  margin-right: 20px;
}

.output-side {
  flex: 1;
  text-align: center;
}

.preset-buttons {
  margin-top: 20px;
  display: flex;
  justify-content: space-around;
}

textarea {
  width: 100%;
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  margin-right: 10px;
  margin-top: 10px;
}

img {
  margin-top: 20px;
  max-width: 100%;
  height: auto;
}
